import React, { Component } from 'react'
import Footer from './Footer'
import Nav from './Nav'
import './../css/aboutUs.css'
import Aos from "aos";
import "aos/dist/aos.css";
import { Link } from "react-router-dom";
import Slider from "react-slick";
import "slick-carousel/slick/slick.css"; 
import "slick-carousel/slick/slick-theme.css";
export default class AboutUs extends Component {
    componentDidMount() {
        Aos.init({ duration: 2000 });
    }
    render() {
        const settings = {
            autoplay: true,
            autoplaySpeed: 1000,
            infinite: true,
            slidesToShow: 1,
            slidesToScroll: 1,
            speed: 1500,
            
        };

        return (
            <div>
                <Nav />
                <div>
                    <div className="AboutUs-pageheader text-center">
                        <h1>About Us - Sanjalika Water Park</h1>
                        <ul className="breadcrumb">
                            <li className="breadcrumb-item home">
                                <Link to="#" title="Homepage">Homepage</Link>
                            </li>
                            <li className="breadcrumb-item active">About Us</li>
                        </ul>
                    </div>
                    <div className="AboutUs-content">
                        <div className="container">
                            <div className="row">
                                <div className="col-lg-8 col-md-9 col-12 detail-content">
                                    <p><strong>Sanjalika Water Park – A pinnacle of amusement, excitement and endless joys!</strong></p>
                                    <p>
                                        <img className="img-fluid img" src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/DSC07073.jpg" alt="" width={830} height="553.19" />
                                    </p>
                                    <p>
                                        Spanning 214ha in total, Sanjalika Water Park is world-class amusement and recreational complex that consists of two main areas:
                                        The Coastal Amusement Park and Amusement Park on Ba Deo Hill, which are connected together with a unique cabe car system.
          </p>
                                    <p>
                                        The Sanjalika Water Park is a brainchild of Sanjalika Group, one of the top-notch companies in investment and operation of amusement
                                        and recreational compounds, which have carved their marks through Sanjalika Ba Na Hills, Sanjalika Danang Wonders or Sanjalika Fansipan Legend…
                                        Given the experiences and prestige of Sanjalika, Sanjalika Water Park is expected to become a top and ideal destination.
          </p>
                                    <p data-aos="fade-up">
                                        Let’s get east for the unlimited Sanjalika Water Park, including two major compounds connected together with a unique cable car system:
          </p>
                                    <p data-aos="fade-up" >
                                        The Coastal Amusement Park on scenic Bai Chay Beach spans up to 169ha and consists of various attractive facilities such as the Dragon Park
                                        – an extreme game zone and the most spectacular roller coaster of Vietnam; an exciting Typhoon Water Park called the Tornado Bay;
                                        and the unwinding coast of Ha Long, encircled with the sapphire water and silky white sandy beach.
          </p>
                                    <p data-aos="fade-up">
                                        A sea-crossing cable car system stretching 2km carries passengers to Ba Deo Amusement Park, a complex of exciting games
                                        and most delightful amusement facilities. Some must-visits on Ba Deo Hill include the Zen Garden,
                                        indoor game zone The Mysterious Castle and in particular the Sun Wheel, one of the tallest Ferris wheels of the world.
          </p>
                                    <p data-aos="fade-up">
                                        <img className="img-fluid img" src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/WM2P0045.jpg" alt="" width={830} height="553.19" />
                                    </p>
                                    <p data-aos="fade-up">Peculiar cuisine</p>
                                    <p data-aos="fade-up">
                                        A catering chain in Sanjalika Water Park is diverse enough, ranging from local treats to more exotic fusion ones that
                                        cater to all possible palates of visitors. It will be a perfect destination for you all to explore and relish most inspiring
                                        foods in Sanjalika Water Park.
          </p>
                                    <p>&nbsp;</p>
                                    <p data-aos="fade-up">
                                        <img className="img-fluid img" src="https://halongcomplex.sunworld.vn/wp-content/uploads/2021/03/T42021-01.jpg" alt="" width={830} height={830} />
                                    </p>
                                    <p>&nbsp;</p>
                                    <i className="fas fa-star float-right aboutus-star" style={{ color: 'red' }}> 5/5</i>
                                    <i className="fas fa-star float-right aboutus-star" style={{ color: 'red' }} />
                                    <i className="fas fa-star float-right aboutus-star" style={{ color: 'red' }} />
                                    <i className="fas fa-star float-right aboutus-star" style={{ color: 'red' }} />
                                    <i className="fas fa-star float-right aboutus-star" style={{ color: 'red' }} />
                                </div>
                                <div className="col-lg-4 col-md-3 col-12 side-content">
                                    <section className="widget">
                                        <section className="info">
                                            <h2 className="text-center pb-2" style={{ fontSize: '20px' }}>Business hours</h2>
                                            <div className="single-item pl-3 pr-3 mt-2 mb-2">
                                                <Slider ref={slider => (this.slider = slider)} {...settings}>
                                                    <div className="slick-item text-center">
                                                        <span style={{ fontWeight: 'normal' }}>Dragon Park</span>
                                                        <div className="time-activity">
                                                            <time className="time float-left ml-5">14:00</time>
                                                            <span>to</span>
                                                            <time className="time float-right mr-5">21:00</time>
                                                        </div>
                                                    </div>
                                                    <div className="slick-item text-center">
                                                        <span style={{ fontWeight: 'normal' }}>Water Park</span>
                                                        <div className="time-activity">
                                                            <time className="time float-left ml-5">14:00</time>
                                                            <span>to</span>
                                                            <time className="time float-right mr-5">21:00</time>
                                                        </div>
                                                    </div>
                                                    <div className="slick-item text-center">
                                                        <span style={{ fontWeight: 'normal' }}>Sanjalika Gallery</span>
                                                        <div className="time-activity">
                                                            <time className="time float-left ml-5">14:00</time>
                                                            <span>to</span>
                                                            <time className="time float-right mr-5">21:00</time>
                                                        </div>
                                                    </div>
                                                </Slider>
                                            </div>
                                            <address className="address pt-2">
                                                Address:
                <span style={{ fontWeight: 'normal' }}>No. 9 Ha Long Street, Bai Chay Ward, Ha Long City, Quang Ninh Province.</span>
                                            </address>
                                            <div className="hotline">
                                                Hotline: <span style={{ fontWeight: 'normal' }}>0989 200 699</span>
                                                <p />
                Email: <span style={{ fontWeight: 'normal' }}>Sanjalika@waterworld.com</span>
                                            </div>
                                        </section>
                                        <h3>Experience</h3>
                                        <div className="list-box">
                                            <div className="box">
                                                <Link to="//QueenCableCars" title="Queen Cable Cars & The Mysterious Hills">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/cap-treo-nu-hoang.png)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/cap-treo-nu-hoang.png" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Queen Cable Cars &amp; The Mysterious Hills</p>
                                                </Link>
                                            </div>
                                            <div className="box">
                                                <Link to="/DragonPark" title="Dragon Park">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/PHI-LONG-THAN-TOC-DRAGON-RUN-2.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/PHI-LONG-THAN-TOC-DRAGON-RUN-2.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Dragon Park</p>
                                                </Link>
                                            </div>
                                            <div className="box" data-aos="fade-up">
                                                <Link to="/WaterPark" title="Water Park">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/WM2P0809.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/WM2P0809.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Water Park</p>
                                                </Link>
                                            </div>
                                            <div className="box" data-aos="fade-up">
                                                <Link to="/Beach" title="Sanjalika Beach">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/17861745_1450815441659147_7614405696089232410_n.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/17861745_1450815441659147_7614405696089232410_n.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Sanjalika Beach</p>
                                                </Link>
                                            </div>
                                            <div className="box" data-aos="fade-up">
                                                <Link to="/LightHouse" title="Sanjalika Lighthouse">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/23231094_1487023091392825_1991457985141299282_n.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/23231094_1487023091392825_1991457985141299282_n.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Sanjalika Lighthouse</p>
                                                </Link>
                                            </div>
                                            <div className="box" data-aos="fade-up">
                                                <Link to="/OldQuarterKiosks" title="Sanjalika Old Quarter Kiosks">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/18920473_247199909096275_7482388689173386718_n.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/18920473_247199909096275_7482388689173386718_n.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Sanjalika Old Quarter Kiosks</p>
                                                </Link>
                                            </div>
                                            <div className="box" data-aos="fade-up">
                                                <Link to="/FoodZone" title="Food & Shopping">
                                                    <div className="img-box" style={{ backgroundImage: 'url(https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/WM2P0029.jpg)' }}>
                                                        <img src="https://halongcomplex.sunworld.vn/wp-content/uploads/2018/10/WM2P0029.jpg" className="img-fluid w-100" alt="" />
                                                        <div className="aboutus-button-plus"><i className="fas fa-plus" /></div>
                                                    </div>
                                                    <p className="title">Food &amp; Shopping</p>
                                                </Link>
                                            </div>
                                        </div>
                                    </section>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>

                <Footer />
            </div>
        )
    }
}
